<template>
  <PokemonTierView v-if="isDexLoaded"></PokemonTierView>
  <section
    v-else
    class="explore__items"
  >
    <div class="lds-ellipsis">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  </section>
</template>
<script>
import PokemonTierView from "@/components/Pokemon";
import axios from "axios";

export default {
  title: "Battle Dex",
  components: {
    PokemonTierView
  },
  created() {
    if (!this.isDexLoaded) {
      const tier = this.$route.params.tier
      axios.get(`/pokemon/${tier}`).then(res => {
        this.$store.commit("dex/SET_BATTLEDEX", res.data.pokemon);
        this.$router.push({ path: `/dex/${tier}` });
        this.$store.commit("dex/SET_LOADING_STATE", true);
      });
    }
  }
};
</script>